<template>
  <div v-if="album" class="base-news-layout-container">

    <div class="album-title one-ellipsis">
      {{ album.name }}
    </div>

    <div class="content f-col-between">
      <div
        v-if="albumDetail && albumDetail.items.length > 0"
        class="items f-col-between"
      >
        <div
          v-for="(section, index) in albumDetail.items.slice(0, 3)"
          :key="section._id"
        >
          <div
            class="f-i-c"
          >
            <img src="@/assets/images/news_layout_item_icon.png" alt="">
            <span class="one-ellipsis">
              {{ section.title }}
            </span>
          </div>
        </div>
      </div>

      <div class="play-all f-c">
        <img src="@/assets/images/news_layout_play_icon.png" alt="">
        <span>播放全部</span>
      </div>
    </div>

    <div
      class="cover"
      :style="{ 'background-image': `url( ${album.cover} )` }"
    >
      <p
        :class="['update', false ? 'hot' : '']"
      >
        {{ album.contentType | fltTemplateName }}
      </p>
      <p v-if="album.lastPlayTime" class="recent">
        {{ album.lastPlayTime | fltTimeInterval }}听过
      </p>
    </div>

  </div>
</template>

<script>
import { apiAlbumDetail } from '../../service/api/program';
import { Toast } from 'vant';
import Vue from 'vue';

Vue.use(Toast);

export default {
  props: {
    album: {
      type: Object,
      default: () => null
    }
  },

  data() {
    return {
      albumDetail: null
    }
  },

  mounted() {
    this.getAlbumDetail( this.album._id );
  },

  methods: {
    async getAlbumDetail(albumId) {
      const albumDetailResponse = await apiAlbumDetail( albumId );
      if (albumDetailResponse.type === 'fail') return Toast( '服务器错误，请联系客服' );
      this.albumDetail = albumDetailResponse.data;
    }
  }
}
</script>

<style lang="scss" scoped>
.base-news-layout-container {
  width: 100%;
  background: rgba(255,255,255,1);
  position: relative;
  
  .album-title {
    width: 100%;
    font-size: 16px;
    font-weight: 600;
    color: rgba(51,51,51,1);
  }
  
  .cover {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 84px;
    height: 112px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    border-radius: 5px 0px 0px 5px;
    box-shadow: 0 0 1px #dfdfdf inset;

    .update {
      position: absolute;
      right: 0;
      top: 0;
      background: rgba(0,0,0,0.4);
      border-bottom-left-radius: 5px;
      border-top-right-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 0 5px;
    }
    .hot {
      background: rgba(214,66,66,1);
    }
    .recent {
      position: absolute;
      left: 0;
      bottom: 0;
      background: rgba(0,0,0,0.4);
      border-bottom-left-radius: 5px;
      border-top-right-radius: 5px;
      font-size: 10px;
      color: rgba(255,255,255,1);
      padding: 0 5px;
    }
  }

  .content {
    width: calc(100% - 85px);
    height: 115px;
    margin-top: 10px;
    box-sizing: border-box;
    position: relative;

    .items {
      height: 80px;
      width: 100%;
      
      img {
        width: 13px;
        height: 13px;
      }
      span {
        font-size: 14px;
        color: rgba(102,102,102,1);
        margin-left: 5px;
      }
    }

    .play-all {
      width: 80px;
      height: 22px;
      background: rgba(244,186,65,1);
      border-radius: 11px;
      padding: 4px 8px;
      box-sizing: border-box;

      img {
        width: 8px;
        height: 8px;
      }
      span {
        margin-left: 3px;
        font-size: 11px;
        color: rgba(255,255,255,1);
      }
    }
  }
}
</style>


